@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
html,body{margin:0;padding:0;width:100%;height:100%;font-family:"Noto Sans KR",sans-serif;font-size:calc(15px + (100vw - 1500px)/400);background-image:url(/static/media/k9-thunder.12340e43.jpg);background-repeat:no-repeat;background-size:cover;background-position:center center;background-attachment:fixed}#root{height:100%;display:flex;justify-content:center;align-items:flex-start}
#now-time{background-image:url(/static/media/camouflage.e8bc64bd.png);background-position:41% 97%;display:flex;flex-direction:column;width:100%}#now-title{margin:0 0;font-size:2rem;line-height:2rem}#now-timer{margin:.5rem auto 0;font-size:4rem;line-height:4rem}
#time-left{background-image:url(/static/media/camouflage.e8bc64bd.png);width:100%;display:flex;flex-direction:column;align-items:flex-start}.left-title{display:inline;margin:0;font-size:3rem;line-height:3rem}.left-text{display:inline;margin:.5rem 0 0;text-align:justify;line-height:4.5rem;font-size:4rem}@media(min-width: 335px)and (max-width: 757px){.left-text{font-size:3.5rem}.left-text#left-day{display:block;text-align:left}.left-text#left-time{display:block;text-align:left}}.progress-group{margin-top:1rem;width:100%;display:flex;justify-content:center;align-items:center}.left-progress{display:inline;width:100%;margin-right:.5rem;-webkit-appearance:none;appearance:none}.left-progress::-webkit-progress-bar{background:#f0f0f0;border-radius:10px}.left-progress::-webkit-progress-value{border-radius:4px;background:#55614f;background:linear-gradient(to right, #6f6e2b, #6B8E23)}.left-percent{display:inline;font-size:1rem;line-height:1rem}
.container{max-width:80rem;display:flex;flex-direction:column;justify-content:center;align-items:center;overflow:hidden}.container .some-blank{height:4rem}.section{box-sizing:border-box;max-width:46rem;margin:0 auto 1rem;padding:1rem;border-radius:15px;color:#fff;text-align:center}
